import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import CssBaseline from '@mui/material/CssBaseline';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close'
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import { ListItemButton } from '@mui/material';
import logo from '../logoName.png';
import styles from './globalNav.module.scss'

const drawerWidth = 240;

const links = [{
    name: "merch",
    url: 'https://candorband.square.site/',
    
}, {
    name: 'music',
    url: 'https://linktr.ee/CandorBand'
}]

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    flexGrow: 1,
    backgroundColor: 'black',
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: `-${drawerWidth}px`,
    ...(open && {
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginLeft: 0,
    }),
  }),
);

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  boxShadow: 'none',
 
  textAlign: 'center',
  backgroundColor: 'transparent',
  backgroundImage: 'none',
  ...(open && { 
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const StyledDrawer = styled(Drawer)(({theme}) => ({
    borderRight: 'none'
}))


  

export default function PersistentDrawerLeft(props) {
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      
      <StyledDrawer
        sx={{
          borderRight: 'none !important',  
          width: drawerWidth,
          flexShrink: 0,
          '>*': {borderRight: 'none !important'},
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
       
        <List>
          {links.map((link, index) => (
            <ListItem button key={link.name} disablePadding>
             <ListItemButton component='a' href={link.url}>
                <ListItemText primary={link.name} />
             </ListItemButton>
            </ListItem>
          ))}
        </List>
       
      </StyledDrawer>
      <Main open={open}>
        {/* <DrawerHeader /> */}
        <AppBar position="absolute" open={open}>
        <Toolbar
            sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}
        >
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={open?handleDrawerClose: handleDrawerOpen}
            edge="start"
            sx={{ mr: 2  }}
          >
            {open ? <CloseIcon/> : <MenuIcon /> }
          </IconButton>
          
          <img className={styles.logo} src={logo} alt="Candor Logo"></img>
          <div className={styles.placeholder}
            
            aria-label="positioning"
            
          />
        </Toolbar>
      </AppBar>
        {props.children}
      </Main>
    </Box>
  );
}
